.super-games {
  display: flex;
  color: $color-default-white;
}

.super-games__info {
  width: 500px;
  max-height: 500px;
  background-color: $color-torch-red;
  padding: 85px 78px;
  position: relative;
}

.super-games__details {
  position: absolute;
  top: 80px;
  left: 329px;
}

.super-games__date {
  margin-bottom: 18px;
}

.super-games h2 {
  font-family: $ff-alumni;
  font-weight: 600;
  font-size: 82px;
  line-height: 79px;
  letter-spacing: 0.03em;
  margin: 0 0 30px 13px;
  text-transform: uppercase;
}

.super-games h4 {
  font-family: $ff-roboto;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  margin: 0;
  margin-bottom: 5px;
  position: relative;
}

.super-games__date-text,
.super-games__time-text {
  border-top: 2px solid $color-default-white;
}

.super-games__date-text {
  display: flex;
  gap: 7px;
  width: 64px;
}

.super-games__date-text span {
  max-width: 40px;
}

.super-games p {
  font-family: $ff-roboto;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  margin: 0 0 44px;
}

.super-games span {
  font-family: $ff-roboto;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}

.super-games strong {
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 40px;
  line-height: 44px;
}

.super-games a {
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  color: $color-default-white;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  display: block;
  padding: 25px 108px;
  border: 4px solid $color-default-white;
  user-select: none;
}

.super-games a:hover,
.super-games a:focus {
  background-color: $color-sapphire;
  outline: none;
}

.super-games a:active {
  background-color: $color-egyptian-blue;
}

.super-games picture {
  background-color: $color-sapphire;
}
